<template>
  <div class="countdown-container">
    <h3 class="countdown-title">–¢—É–π–≥–∞ “õ–æ–ª–≥–∞–Ω –≤–∞“õ—Ç:</h3>
    <div class="countdown-timer" v-if="!isWeddingDay">
      <div class="time-block">
        <span class="time-value">{{ days }}</span>
        <span class="time-label">–∫—É–Ω</span>
      </div>
      <div class="time-separator">:</div>
      <div class="time-block">
        <span class="time-value">{{ hours }}</span>
        <span class="time-label">—Å–æ–∞—Ç</span>
      </div>
      <div class="time-separator">:</div>
      <div class="time-block">
        <span class="time-value">{{ minutes }}</span>
        <span class="time-label">–º–∏–Ω—É—Ç</span>
      </div>
      <div class="time-separator">:</div>
      <div class="time-block">
        <span class="time-value">{{ seconds }}</span>
        <span class="time-label">—Å–µ–∫—É–Ω–¥</span>
      </div>
    </div>
    <div class="countdown-message" v-else>
      <span class="heart-pulse">üíñ</span> –ë—É–≥—É–Ω –±–∏–∑–Ω–∏–Ω–≥ –∞–∑–∏–∑ —Ç—É–π –∫—É–Ω–∏–º–∏–∑! <span class="heart-pulse">üíñ</span>
    </div>
    <div class="event-time" v-if="isWeddingDay">
      <p>–ù–æ–Ω—É—à—Ç–∞ —Å–æ–∞—Ç–∏: 6:00</p>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      weddingDate: new Date("2025-06-22T00:00:00"), 
      now: new Date(),
      timer: null
    };
  },
  computed: {
    days() {
      return Math.floor(this.timeDiff / (1000 * 60 * 60 * 24));
    },
    hours() {
      return Math.floor((this.timeDiff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    },
    minutes() {
      return Math.floor((this.timeDiff % (1000 * 60 * 60)) / (1000 * 60));
    },
    seconds() {
      return Math.floor((this.timeDiff % (1000 * 60)) / 1000);
    },
    timeDiff() {
      return this.weddingDate - this.now;
    },
    isWeddingDay() {
      const weddingDay = new Date(this.weddingDate);
      return (
        this.now.getDate() === weddingDay.getDate() &&
        this.now.getMonth() === weddingDay.getMonth() &&
        this.now.getFullYear() === weddingDay.getFullYear()
      );
    }
  },
  mounted() {
    this.timer = setInterval(() => {
      this.now = new Date();
    }, 1000);
  },
  beforeUnmount() {
    clearInterval(this.timer);
  }
};
</script>

